import React from 'react';
import ReactDOM from 'react-dom';
import "./Clients.css"
import {ClientStats} from "../../Structures/ClientStats";
import {Notifications} from "../../Apis/Notifications";
import YitPage from "../../Components/YitPage/YitPage";
import YitComponent from "../../Structures/YitComponent";
import YitDataTable from "../../Components/YitDataTable/YitDataTable";
import Chip from "@mui/material/Chip";

class Clients extends YitComponent {

    constructor(props){
        super(props);
        this.state = {
            rawData: [],
            columns: [
                {title: 'Id', field: "id"},
                {title: 'Name', field: "fullName"},
                {title: 'E-mail', field: "mail"},
                {title: 'Phone',   field: "phone" },
                {title: 'Joint At',   field: "createdAt", render: rowData => rowData['createdAt'].substr(0, 10), editable: false},
                {
                    title: 'Stat',   field: "stat",
                    lookup: { 0: 'New', 1: 'Active', 2: "Frozen" },
                    render: (rowData)=>{
                        let result, color
                        switch (rowData.stat){
                            case 0: result = 'New'; color='green'; break;
                            case 1: result = 'Active'; color= 'darkblue'; break;
                            case 2: result = 'Frozen'; color="red"; break;
                            default:result = "Unknown"; color="red"
                        }
                        return(<Chip  label={result}  style={{width: 120, backgroundColor: color, color:"white", fontWeight: "bold"}}/>)
                    }
                },

            ],
            data: [],
            selectedRowId: null,
            open: false
        }
        this.dataTable = React.createRef()
    }

    handleClose = (event, reason) => {

    };

    async componentWillMount(){
        try{
            let clientsList = await this.MODELS._Client.getAll(0, 20)
            if(clientsList != null){
                this.setState({

                    data: clientsList
                })
                this.currentPage.current.setState({
                    isLoading: false
                })
            }else {

            }
        }catch (e){

        }
    }

    async componentDidMount() {

    }


    async changeStat(rowData, stat){
        try{
            let updateResult = await this.MODELS._Client.update(rowData.id, {stat: stat})
            if(updateResult){
                Notifications.drawActionResult("pagesNotifications", "Stat Changed successfully", "success")
                this.dataTable.current.tableRef.current.onQueryChange({})
            }
        }catch (e){

        }
    }

    async deleteEntry(rowData) {
        try {
            let updateResult = await this.MODELS._Client.delete(rowData.id)
            if (updateResult) {
                Notifications.drawActionResult("pagesNotifications", "Client Deleted", "success")}
        } catch (error) {

        }
    }

    async updateClientInfo(newData){
        try{
            let updateOp = await this.MODELS._Client.update(newData.id, newData)
            if(updateOp.finalResult){
                Notifications.drawActionResult("pagesNotifications", "Client Updated", "success")
                this.dataTable.current.tableRef.current.onQueryChange()
            }else{
                Notifications.drawActionResult("pagesNotifications", "Could update Client", "error")
            }
        }catch (e){
            Notifications.drawActionResult("pagesNotifications", "Could not change client info", "error")
        }
    }

    render() {
        return (
            <YitPage
                customClass={"ClientsPage"} customId={"ClientsPage"} title={"Clients"}
                ref={this.currentPage}>

                <YitDataTable
                    ref={this.dataTable}
                    columns={this.state.columns}
                    dataModel={this.MODELS._Client}
                    searchEntry={'fullName'}
                    editable={{
                        onRowUpdate: (newData, oldData) => this.updateClientInfo(newData),
                        onRowDelete: oldData =>
                            new Promise((resolve, reject) => {
                                this.deleteEntry(oldData).then(r => resolve())
                            })
                    }}
                />
            </YitPage>
        )
    }
}

export default Clients
