import React from 'react';
import { GoogleMap,  InfoWindow, Marker } from "@react-google-maps/api";
import Autocomplete from 'react-google-autocomplete';
class YitLocationPicker extends React.Component {
    constructor(props) {
        super(props);
        YitLocationPicker.displayName = "YitLocationPicker"
        this.state = {
            zoom: 15,
            height: 200,
            mapPosition: this.props.center === undefined ? {lat: -3.745, lng: -38.523} : this.props.center,
            markerPosition: this.props.center === undefined ? {lat: -3.745, lng: -38.523} : this.props.center,

        }
    }


    componentDidMount(){

    };



    onChange = (event) => {
        this.setState({ [event.target.name]: event.target.value });

    };

    onMarkerDragEnd = (e) => {

        let newLat = e.latLng.lat(), newLng = e.latLng.lng();

        this.setState({
            markerPosition: {
                lat: newLat,
                lng: newLng
            },
            mapPosition: {
                lat: newLat,
                lng: newLng
            },
        })
        if(this.props.onDragEnd !== undefined && typeof this.props.onDragEnd == "function"){
            this.props.onDragEnd(e)
        }
    };

    render() {
        return (

                <GoogleMap
                    mapContainerStyle={{height: "40vh", width: "100%"}}
                    zoom={this.state.zoom}
                    center={{ lat: this.state.mapPosition.lat, lng: this.state.mapPosition.lng }}

                >
                    {/* InfoWindow on top of marker */}

                    {/*Marker*/}
                    <Marker
                        google={this.props.google}
                        name={'Dolores park'}
                        draggable={true}
                        onDragEnd={(e)=>{
                            this.onMarkerDragEnd(e)

                        }}
                        position={{ lat: this.state.markerPosition.lat, lng: this.state.markerPosition.lng }}
                    />
                        <InfoWindow
                            onClose={this.onInfoWindowClose}
                            position={{ lat: (this.state.markerPosition.lat + 0.0018), lng: this.state.markerPosition.lng }}
                        >
                            <div>
                                <span style={{ padding: 0, margin: 0 }}>Drag me to choose</span>
                            </div>
                        </InfoWindow>
                    <Marker />

                    {/* <MarkerWithLabel
                            position={{ lat: -34.397, lng: 150.644 }}
                            labelAnchor={new google.maps.Point(0, 0)}
                            labelStyle={{ backgroundColor: "yellow", fontSize: "32px", padding: "16px" }}
                        >
                            <div>Hello There!</div>
                        </MarkerWithLabel> */}


                    {/* For Auto complete Search Box */}

                </GoogleMap>
        )
    }

}

export default YitLocationPicker;