import React from "react";
import ReactDOM from 'react-dom';
import "./Home.css"
import StatBox from "../../Components/StatBox/StatBox";
import AccountTreeIcon from '@mui/icons-material/AccountTree';
import YitPage from "../../Components/YitPage/YitPage";
import YitComponent from "../../Structures/YitComponent";

import ListItem from "@mui/material/ListItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import Switch from "@material-ui/core/Switch";
import List from "@mui/material/List";
import TransactionsGraph from "../../Components/TransactionsGraph/TransactionsGraph";
import YitLocationPicker from "../../Components/YitFullLocationPeeker/YitFullLocationPicker";

const MuiIcons = require('@mui/icons-material');


class Home extends YitComponent{

    constructor(props) {
        super(props);
        this.state = {
            failedLoading: false,
            isLoading: false,
            settings: {},
            statistics: {},
            graphHeight: 0,
            graphWidth: 0
        }
        this.currentPage = React.createRef()
    }
    async changeHolder(e) {
        let nam = e.target.name;
        let val = e.target.checked;
        if (e.target.type === "number") {
            val = parseInt(val)
        }
        let updateOp = await this.MODELS._Setting.update(nam, {dataValue: val})
        if(updateOp.finalResult){
            this.state.settings[nam] = val
            this.setState({
                settings: this.state.settings
            })
        }else {
            this.currentPage.current.pushNotification("Could not update settings", "error", 2000)
        }

    }
    async loadSettings(offset, limit){
        let getAllOp = await this.MODELS._Setting.getAll(offset, limit)
        if(getAllOp.finalResult){
            let temp = {}
            getAllOp.result.forEach(entry =>{
                temp[entry.name] = entry.dataValue
            })
            this.setState({settings: temp})
        }
        this.setState({failedLoading: !getAllOp.finalResult})
    }

    async loadStatistics(actors){
        for (const actor of actors) {
            let getTotal = await actor.loader.count()
            if(getTotal.finalResult){
                this.state.statistics[actor.model] = getTotal.result
                this.setState({statistics: this.state.statistics})
            }
            this.setState({failedLoading: !getTotal.finalResult})
        }
    }

    async loadPrimaryData() {
        let actors = [
            {model: "clients", loader: this.MODELS._Client},
            {model: "stations", loader: this.MODELS._Station},
            {model: "partners", loader: this.MODELS._Partner},
            {model: "transactions", loader: this.MODELS._Transaction},
        ]
        await this.loadSettings(0, 50)
        await this.loadStatistics(actors)
        if(this.state.failedLoading){
            this.currentPage.current.setState({
                failedLoading: true
            })
        }else {
            this.currentPage.current.setState({
                failedLoading: false,
                isLoading: false
            })
        }
    }

    async loadTransactionsData(){
        let getAllOp = this.MODELS._Transaction.getAll(0, 0, 100)
        //let getAllOp = this.MODELS._Transaction.getAll(1, 0, 100)
    }
    setDimensions(){



        let container = document.getElementById('transactionsContainer')
        if(container !== null){
            let y = container.offsetHeight
            let x = container.offsetWidth
            this.setState({
                x:x-20,
                y:y-50
            })
        }

        window.onresize = ()=>{
            let container = document.getElementById('transactionsContainer')
            if(container !== null){
                let y = container.offsetHeight
                let x = container.offsetWidth
                this.setState({
                    x:x-20,
                    y:y-50
                })
            }
        }

    }
    async componentDidMount() {
        await this.loadPrimaryData()
        this.setDimensions()
    }

    render(){
        return(
                <YitPage
                    ref={this.currentPage}
                    title={"Dash Board"}
                    customClass={'Home'}
                    customContentClass={'homeContent'}
                    topOperations={[]}
                    reloadPrimaryInfo={()=>{
                        this.loadPrimaryData().then()
                    }}
                >
                    <div className="StatsContainer">
                        <div className="container">
                            <StatBox
                                title={'Clients'}
                                number={this.state.statistics["clients"]}
                                text={"+7 this month"}
                                icon={<MuiIcons.Person style={{fontSize: 50, color:"wheat"}}/>}

                            />
                        </div>
                        <div className="container">
                            <StatBox
                                number={this.state.statistics["partners"]}
                                text={"All"}
                                title={'Partners'}
                                icon={<MuiIcons.People style={{fontSize: 50, color: "rosybrown"}}/>}
                            />
                        </div>
                        <div className="container">
                            <StatBox
                                title={'Stations'}
                                number={this.state.statistics["stations"]}
                                icon={<MuiIcons.Apps style={{fontSize: 50, color:"black"}}/>}
                                text={"Active Stations"}
                            />

                        </div>
                        <div className="container">
                            <StatBox
                                title={"Transactions"}
                                number={this.state.statistics["transactions"]}
                                icon={<MuiIcons.LocalAtm style={{fontSize: 50, color:"wheat"}}/>}
                                text={"All"}
                            />
                        </div>
                    </div>
                    <div className={"transactionsContainer"} id={"transactionsContainer"}>
                        <b>Rent / return transactions</b>
                        <TransactionsGraph x={this.state.x} y={this.state.y}></TransactionsGraph>
                    </div>
                    <div className="quickSettingsContainer">
                        <List>
                            <ListItem>
                                <ListItemText id="switch-list-label-wifi" primary={<b>Settings</b>} />
                            </ListItem>
                            <ListItem>
                                <ListItemIcon><AccountTreeIcon/></ListItemIcon>
                                <ListItemText id="switch-list-label-wifi" primary="Pratic Power" />
                                <Switch
                                    name={"system"}
                                    edge="end"
                                    onChange={this.changeHolder.bind(this)}
                                    checked={this.state.settings["system"]}
                                    inputProps={{
                                        'aria-labelledby': 'switch-list-label-wifi',
                                    }}
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemIcon>
                                    <MuiIcons.People/>
                                </ListItemIcon>
                                <ListItemText id="switch-list-label-wifi" primary="Client Signup" />
                                <Switch
                                    edge="end"
                                    name={'clientSignUp'}
                                    onChange={this.changeHolder.bind(this)}
                                    checked={this.state.settings["clientSignUp"]}
                                    inputProps={{
                                        'aria-labelledby': 'switch-list-label-wifi',
                                    }}
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemIcon>
                                    <MuiIcons.People/>
                                </ListItemIcon>
                                <ListItemText id="switch-list-label-wifi" primary="Client Login" />
                                <Switch
                                    edge="end"
                                    name={'clientLogin'}
                                    onChange={this.changeHolder.bind(this)}
                                    checked={this.state.settings["clientLogin"]}
                                    inputProps={{
                                        'aria-labelledby': 'switch-list-label-wifi',
                                    }}
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemIcon>
                                    <MuiIcons.Apps />
                                </ListItemIcon>
                                <ListItemText id="switch-list-label-bluetooth" primary="Power Banks Rent" />
                                <Switch
                                    name={"rent"}
                                    edge="end"
                                    onChange={this.changeHolder.bind(this)}
                                    checked={this.state.settings["rent"]}
                                    inputProps={{
                                        'aria-labelledby': 'switch-list-label-bluetooth',
                                    }}
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemIcon>
                                    <MuiIcons.QrCode2 />
                                </ListItemIcon>
                                <ListItemText id="switch-list-label-bluetooth" primary="Card Print" />
                                <Switch
                                    edge="end"
                                    onChange={this.changeHolder.bind(this)}
                                    checked={this.state.settings["print"]}
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemIcon>
                                    <MuiIcons.QrCodeScanner />
                                </ListItemIcon>
                                <ListItemText id="switch-list-label-bluetooth" primary="Card recharge" />
                                <Switch
                                    edge="end"
                                    onChange={this.changeHolder.bind(this)}
                                    checked={this.state.settings["recharge"]}
                                />
                            </ListItem>
                        </List>
                    </div>
                </YitPage>
        )
    }
}
export default Home