import React from "react";
import ReactDOM from "react-dom";
import "./NewStationForm.css"
import Grid from "@mui/material/Grid";
import FormControl from "@mui/material/FormControl";
import YitComponent from "../../../Structures/YitComponent";
import Select from "@mui/material/Select";
import MenuItem from "@material-ui/core/MenuItem";
import YitForm from "../../../Components/YitForm/YitForm";
import InputLabel from "@mui/material/InputLabel";
import YitFragment from "../../../Components/YitFragment/YitFragment";
import TextField from "@material-ui/core/TextField";

class NewStationForm extends YitComponent{
    constructor(props) {
        super(props);
        this.state = {
            isSubmitted: false,
            isSubmitting: false,
            done: false,
            partnersList: [],
            statsList: [
                {label: "active", id: 0},
                {label: "Frozen", id: 1}
            ]
        }


        this.stationTypes = [
            {
                label: "active",
                value   : 0
            },
            {
                label: "inactive",
                value   : 1
            }
        ]
     this.formRef = React.createRef()
    }

    async componentDidMount() {
        let getOp = await this.MODELS._Partner.getAll(0, 999)
        if(getOp.finalResult !== false){
            let partners = getOp.result.rows
            let prepared = partners.map(partner =>{
                return{value: partner.id, label: partner.fullName}
            })
            this.setState({
                partnersList: prepared
            })
        }else {

        }
    }

    async handleSubmit(newData) {
        let createOp = await this.MODELS._Station.create(newData)
        if(createOp.finalResult){
            return true
        }else {
            let error = createOp.error.toString()
            this.formRef.current.setState({
                errMsg: error
            })
           return false
        }
    }

    render() {
     return(
            <YitFragment
                ref={this.fragment}
                customClass={"NewStationForm"}
                customId={"NewStationForm"}
                title={"Station Details"}
                isLoading={false}
                primaryInfoLoader={()=>{this.loadPrimaryData().then()}}
            >
            <YitForm
                ref={this.formRef}
                handleSubmit={this.handleSubmit.bind(this)}
            >
                <Grid container spacing={2}>
                    <Grid item xs={12}><b>New Station</b></Grid>
                    <Grid item xs={6}>
                        <TextField
                            fullWidth required name={"id"}
                            variant={"outlined"}
                            id="outlined-required" label="Public Id" defaultValue={""}
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <FormControl fullWidth>
                            <InputLabel id="demo-simple-select-label">Partner</InputLabel>
                            <Select
                                labelId="demo-simple-select-label"
                                id="currentPartner"
                                label="Partner"
                                name={"currentPartner"}
                                defaultValue={-1}

                            >
                                <MenuItem value={-1} key={-10}>Select Partner</MenuItem>
                                {this.state.partnersList.map((option) => (
                                    <MenuItem value={option.value} key={option.value}> {option.label}</MenuItem>
                                ))}

                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={6}>
                        <FormControl fullWidth>
                            <InputLabel id="demo-simple-select-label">Stat</InputLabel>
                            <Select
                                labelId="demo-simple-select-label"
                                id="demo-simple-select"
                                name={"stat"}
                                label="Stat"
                                defaultValue={-1}
                            >
                                <MenuItem value={-1} key={-100}>Select Stat</MenuItem>
                                {this.state.statsList.map((option) => (
                                    <MenuItem value={option.id} key={option.value}> {option.label}</MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={6}>
                        <TextField
                            fullWidth required name={"price"}
                            variant={"outlined"} type={"number"}
                            id="outlined-required" label="Price" defaultValue={""}
                        />
                    </Grid>

                </Grid>
            </YitForm>
            </YitFragment>
        )


    }
}
export default NewStationForm