import React from 'react';
import {BrowserRouter, Route, Redirect} from "react-router-dom";
import "./Style/yitClasses.css"
import {connect, Provider} from 'react-redux'
import LeftPane from "./Components/LeftPane";
import RightPane from "./Components/RightPane";
import { Switch } from "react-router-dom";
import LoginPage from "./Pages/LoginPage/LoginPage";
import Main from "./Layouts/MAIN/Main";
import _Admin from "./Models/_Admin";
import {HeartBit} from "./Apis/Redux/Actions/Types";
import {Internet} from "./Apis/Redux/Actions/Types";
import {serverStatToProps} from "./Apis/Redux/Reducers";
import {LOCALSTORAGE} from "./Apis/GlobalConfig";
import {authOperations} from "./Apis/authOperations";


const checkServer = () => async dispatch => {
    let heartBit = await _Admin.HeartBit()
    if (heartBit.finalResult) {
        dispatch({
            type: HeartBit,
            payLoad: true
        })
    } else {
        dispatch({
            type: HeartBit,
            payLoad: false
        })
    }
    let internet = navigator.onLine

    dispatch({
        type: Internet,
        payLoad: internet
    })

}
class App extends React.Component{
    constructor(props) {
        super(props);

        let adminMail = LOCALSTORAGE.getItem("adminMail")
        let adminToken = LOCALSTORAGE.getItem("adminToken")
        authOperations.isLoggedIn() ?   void(0) : window.location.assign("/Login")
    }
    componentDidMount() {

    }

    async componentWillMount() {
        let test = this.props.checkServer
        setInterval(()=>{test()}, 1000)
    }

    render() {


    return (
        <React.Fragment>
            <BrowserRouter>
                <Switch>
                    <Route path={"/"} >
                        <Main/>
                    </Route>
                </Switch>
            </BrowserRouter>
        </React.Fragment>
    );
    }
}

export default connect(serverStatToProps,  {checkServer})(App);
