import React from "react";
import "./YitFileUploader.css"
import Button from "@material-ui/core/Button";
import IconButton from '@mui/material/IconButton';
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';


const Image = ({url}) =>{
    let container = {
        width: 200,
        height: 200,
        margin: "10px",
        borderRadius: 20
    }
    const imageStyle = {
        width: "100%",
        height: "100%",
        borderRadius: 20
    }
     return(
         <div style={container}>
             <img src={url} style={imageStyle}/>
         </div>
     )
}

class YitFileUploader extends React.Component {
    constructor(props) {

        super(props);
        this.sharedData = {}
        this.state = {
            currentImage: '',
            isSubmitted: false,
            isSubmitting: false,
            done: false,
            partnersList: [],
            markers: [],
            ImagesList: this.props.ImagesList !== undefined ? this.props.ImagesList : []
        }

        this.stationTypes = [
            {
                label: "active",
                value: 0
            },
            {
                label: "inactive",
                value: 1
            }
        ]
        this.formRef = React.createRef()
    }

    async componentDidMount() {

    }
    isCompleted(){
        if(this.sharedData !== undefined && this.sharedData !== {}){
            return true
        }else {
            alert("no images selected")
            return false
        }
    }
    onImageSelect(e){
        console.log(e.target.files)

        for (let i = 0; i < e.target.files.length; i++){
            const file = e.target.files[i];
            let Image =  URL.createObjectURL(file)
            this.state.ImagesList.push(Image)

        }

        // let imageTag  = document.getElementById('imageTag')
        this.setState({
            ImagesList:  this.state.ImagesList
        })
        this.sharedData = this.state.ImagesList
    }


    async handleSubmit(newData){
        let password = this.generatePassword()
        newData['password'] = password
        let createOp = await this.MODELS._Partner.create(newData)
        if (createOp.finalResult) {
            return true
        } else {

            this.formRef.current.setState({
                errMsg: createOp.error
            })
            return false
        }
    }

    renderContent(list){
        return(
        list.map(image =>{
            return (<Image url={image}></Image>)
        }))
    }

    render() {
        return (
            <div className={'YitFilePicker'}>
                {this.renderContent(this.state.ImagesList)}
                {

                }
            </div>
        )
    }
}

export default YitFileUploader