import React from 'react';
import "./NewPartner.css";
import ReactDOM from 'react-dom';


//Icons

import {Link} from "react-router-dom";




import {Notifications} from "../../../Apis/Notifications";
import YitStepper from "../../../Components/YitStepper/YitStepper";
import {ClientStats} from "../../../Structures/ClientStats";
import NewPartnerForm from "../NewPartnerForm/NewPartnerForm";
import YitFilePicker from "../../../Components/YitFilePicker/YitFilePicker";
import YitFileUploader from "../../../Components/YitFileUploader/YitFileUploader";

const MuiIcons = require("@mui/icons-material")

function statToString(stat) {
    let result = ""
    ClientStats.forEach((clientStat) => {
        if (clientStat.value == stat) {
            result = clientStat.label
        }
    })
    return result
}


class NewPartner extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            open: false,
            filesToUpload : []
        }
        this.dataTable = React.createRef()
        this.stepperRef = React.createRef()
    }

    handleClose = (event, reason) => {

    };

    async loadPartners() {
        let getOp = await this.MODELS._Partner.getAll(0, 20)
        if (getOp.finalResult) {
            let partnersList = getOp.result
            this.setState({
                failedLoading: false,
                data: partnersList
            })
        } else {

            this.setState({
                failedLoading: true
            })
        }

    }

    async loadPrimaryData() {
        await this.loadPartners()
        if (this.state.failedLoading) {

            this.currentPage.current.setState({
                failedLoading: true
            })
        } else {
            this.currentPage.current.setState({
                failedLoading: false,
                isLoading: false
            })
        }

    }

    async componentWillMount() {
        //await this.loadPrimaryData()

    }

    async componentDidMount() {


    }


    async changeStat(rowData, stat) {
        try {
            let updateResult = await this.MODELS._Partner.update(rowData.id, {stat: stat})
            if (updateResult) {
                Notifications.drawActionResult("pagesNotifications", "Stat Changed successfully", "success")
                this.dataTable.current.tableRef.current.onQueryChange()
            }
        } catch (error) {

        }
    }

    async deleteEntry(rowData, stat) {
        try {
            let updateResult = await this.MODELS._Partner.delete(rowData.id)
            if (updateResult) {
                Notifications.drawActionResult("pagesNotifications", "Partner Deleted", "success")
                this.dataTable.current.tableRef.current.onQueryChange({})
            }
        } catch (error) {

        }
    }
    async handleSubmit(newData){
        let password = this.generatePassword()
        newData['password'] = password
        let createOp = await this.MODELS._Partner.create(newData)
        if (createOp.finalResult) {
            return true
        } else {
            alert(JSON.stringify(createOp))
            this.formRef.current.setState({
                errMsg: createOp.error
            })
            return false
        }
    }

    render() {
        return (

            <div className={"newPartner"}>
            <YitStepper
                ref={this.stepperRef}
                onFinish={this.handleSubmit}
                onFinishView={ <YitFileUploader ImagesList={this.state.filesToUpload}></YitFileUploader>}
                steps = {[
                    {label: 'General information', optional:false},
                    {label: 'Images', optional:false},
                ]}>
                <NewPartnerForm></NewPartnerForm>
                <YitFilePicker></YitFilePicker>
            </YitStepper>
            </div>


        )
    }
}

export default NewPartner