import React from "react";
import "../Style/NavBar.css"
import {Link, NavLink} from "react-router-dom";

import routes from "../routes";
function createMenu (routes) {
    return (
        routes.map((prop, index) => {
            if(prop.parametrised === undefined){
                return (
                    <NavLink className={"navEntry"} to={prop.path} key={index}>
                        <div className={"navIcon"}>{<prop.icon style={{fontSize: "xx-large"}}/>}</div>
                        <span className={"navText"}>{prop.name}</span>
                    </NavLink>
                );
            }
            return

        })
    )
};

class NavBar extends React.Component{

    constructor(props){
        super(props);
    }

    componentDidMount() {

    }
    render(){
        return(
            <div className={"NavBar"}>
                {createMenu(routes)}
            </div>
        )
    }
}
export default NavBar