
import React from "react";
import ReactDOM from 'react-dom';
import "./TabsWidget.css"
import Button from "@mui/material/Button";





class  TabsWidget  extends React.Component {
    static id = 0
    constructor(props) {
        super(props);
        this.state = {
            tabsWidgetId: "TabsWidget" +TabsWidget.id,
            tabContentId: "tabContent"+TabsWidget.id,
            tabButtonId: "tabButton"+TabsWidget.id,
            currentIndex : 0
        }
        TabsWidget.id++
    }

    changeSelected(selectedIndex){
        let buttonsList = document.getElementsByClassName(this.state.tabButtonId)
        for(let i=0; i<buttonsList.length; i++){
            if(buttonsList[i].id == selectedIndex){
                buttonsList[i].style.color = "darkblue"
                buttonsList[i].style.textDecoration = "underline"
            }else {
                buttonsList[i].style.color = "grey"
                buttonsList[i].style.textDecoration = ""
            }
        }

    }
    generateTabs(){

        return (
        this.props.tabsList.map((tab, index)=>{

            return(
            <div className={"tabTitle"}>
                <div
                    style={{color: "green"}}
                    id={this.state.tabButtonId+index}
                    fullWidth className={"TabsWidgetTab " + this.state.tabButtonId}
                    onClick={async () => {
                        this.setState({
                            currentIndex: index
                        })
                        if (tab.clickHandler != undefined) {
                            tab.clickHandler()
                        }
                    }}
                >{tab.title}</div>
            </div>
            )
        })
        )
    }

     setContent(index){

         this.changeSelected(this.state.tabButtonId+index)
         if(this.props.children.length === undefined){
             //this.tabsRefs[0].current.color = "Primary"
             return(this.props.children)
         }else{
             return(this.props.children[index])
             //if(this.props.tabsList[0].clickHandler != undefined){this.props.tabsList[0].clickHandler()}
         }



        //document.getElementById(this.state.tabButtonId+index).style.color=  "red"

    }

    async componentDidMount() {

    }


    render() {
        return(
            <React.Fragment>
                <section className={"TabsWidget"} id={this.state.tabsWidgetId}>
                    <div className="tabsHeader">
                        {this.generateTabs()}
                    </div>
                    <div className="tabContent" id={this.state.tabContentId}>
                        {this.setContent(this.state.currentIndex)}
                    </div>
                </section>
            </React.Fragment>

        )
    }

}
export default TabsWidget