import React from "react"
class YitStep extends React.Component{
    constructor(props) {
        super(props);
        this.completed = false
    }

    componentDidMount() {

    }

    prepareComponent(comp){
        let  childProps = {ref: this.ref, a:5};
        return React.cloneElement(comp, childProps);
    }

    render() {
        return(
            <div className={"YitStep"}>
                {this.prepareComponent(this.props.children)}
            </div>
        )
    }
}

export default YitStep