import React from "react";
import "./YitView.css"
import Button from "@mui/material/Button";
class YitView extends React.Component{
    constructor(props) {
        super(props);

        this.state =
            {
                active : this.props.active
            }
        this.myRef = React.createRef();
    }

    setActive(state){
        this.setState({
            active : state
        })
    }



    render() {
        if(this.state.active){
            return (
                <div className={"active " + this.props.className} id={this.props.id} ref={this.myRef}>
                    {this.props.children}
                </div>
            )
        }
        return (
           <div  className={"active " + this.props.className} id={this.props.id} ref={this.myRef}>
               {this.props.children}
               <div className={"anActive"}>
                   {this.props.anActiveContent}
               </div>

           </div>
        )
    }
}
export default YitView