import React from "react";
import CharOptions from "./CharOptions";
import YitComponent from "../../Structures/YitComponent";
import YitFragment from "../YitFragment/YitFragment";
let LineChart = require("react-chartjs").Line;


class TransactionsGraph extends YitComponent{
    constructor(props) {
        super(props);
        this.state = {
            failedLoading : false,
            charData :  {
                labels: this.weekDays().weekDays,
                datasets: [
                    {
                        label: "Rent Transactions",
                        fillColor: "rgba(220,220,220,0.2)",
                        strokeColor: "rgba(220,220,220,1)",
                        pointColor: "rgba(220,220,220,1)",
                        pointStrokeColor: "#fff",
                        pointHighlightFill: "#fff",
                        pointHighlightStroke: "rgba(220,220,220,1)",
                        data: [5, 12, 16, 22, 18, 50, 10]
                    },
                    {
                        label: "Return Transaction",
                        fillColor: "rgba(151,187,205,0.2)",
                        strokeColor: "rgba(151,187,205,1)",
                        pointColor: "rgba(151,187,205,1)",
                        pointStrokeColor: "#fff",
                        pointHighlightFill: "#fff",
                        pointHighlightStroke: "rgba(151,187,205,1)",
                        data: [5, 12, 16, 22, 18, 50, 10]
                    }
                ]
            }
        }
        this.fragmentRef = React.createRef()

    }

    async componentDidMount() {
        let rentTransactions = await this.loadTransactionsData(0, 0, 100)
        let returnTransactions = await this.loadTransactionsData(1, 0, 100)

        this.state.charData['datasets'][0]['data'] = rentTransactions
        this.state.charData['datasets'][1]['data'] = returnTransactions
        this.setState({
            charData: this.state.charData
        })

        this.fragmentRef.current.setState({
            isLoading : false,
            failedLoading : this.state.failedLoading
        })
    }

    weekDays(){
        let baseDate = new Date();
        let weekDays = [];
        let indexes = []
        for(let i = 0; i < 7; i++)
        {
            baseDate.setDate(baseDate.getDate() - 6);
            weekDays.push(
                baseDate.toLocaleDateString("en", { weekday: 'long' })
            );
            indexes.push(baseDate.getDay())

        }
        return {weekDays, indexes}
    }

    prepareData(){

    }


    async loadTransactionsData(operation, offset, limit){
        let getAllOp = await this.MODELS._Transaction.getAll(operation, offset, limit)
        let acceptedDate = new Date();
        acceptedDate.setDate(acceptedDate.getDay()-6)
        let data = [0, 0, 0, 0, 0, 0, 0]
        if(getAllOp.finalResult){
            let transactions = getAllOp.result
            transactions.forEach(transaction =>{
                let transactionDate = new Date(transaction['createdAt'])
                if(transactionDate > acceptedDate){
                    data[transactionDate.getDay()] = data[transactionDate.getDay()] + 1
                }
            })
            let tempData = data.slice()
            this.weekDays().indexes.forEach((entry, index) =>{
                data[entry-1] = tempData[index]
            })
            tempData = null

        }
        this.setState({
            failedLoading: !getAllOp.finalResult
        })
        return data
    }

    render() {
        return(
            <>
                <YitFragment
                    ref={this.fragmentRef}
                >
                    <LineChart
                        data={this.state.charData}
                        options={CharOptions}
                        width={this.props.x}
                        height={this.props.y}
                    />
                </YitFragment>

            </>
        )
    }
}

export default TransactionsGraph