import React from "react";
import Grid from "@mui/material/Grid";
import Button from "@mui/material/Button";
import YitComponent from "../../Structures/YitComponent";
import SaveIcon from "@mui/icons-material/Save";
import LoadingButton from "@mui/lab/LoadingButton";
import {Notifications} from "../../Apis/Notifications";
import DynamicSubmitButton from "../DynamicSubmitButton/DynamicSubmitButton";

function  drawSubmit(isSubmitting){
    if(!isSubmitting){
        return (
            <Button fullWidth startIcon={<SaveIcon />} variant="contained" color="primary" type={"submit"}>
                Submit
            </Button>
        )
    }
    else {
        return (

            <LoadingButton
                fullWidth
                loading
                loadingPosition="start"
                startIcon={<SaveIcon />}
                variant="outlined"
            >
                Requesting
            </LoadingButton>
        )
    }
}


const DoneMsg = () =>{
    return (<div className="success-animation">
        <svg className="checkmark" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 52 52">
            <circle className="checkmark__circle" cx="26" cy="26" r="25" fill="none"/>
            <path className="checkmark__check" fill="none" d="M14.1 27.2l7.1 7.2 16.7-16.8"/>
        </svg>
    </div>)
}
class YitTcpForm extends YitComponent{
    constructor(props) {
        super(props);
        this.state = {
            isSubmitted: false,
            isSubmitting: false,
            done: false,
            newInfo: {},
        }
        this.submitButton = React.createRef()
    }

    changeHolder(e) {
        let nam = e.target.name;
        let val = e.target.value;
        if(e.target.type === "number"){
            val = parseInt(val)
        }
        this.state.newInfo[nam]= val
    }

    drawResult(){
        this.props.view.pushNotification(this.props.errorMsg, "error", 2000)
        //Notifications.drawActionResult("pagesNotifications", this.props.errorMsg, "error")
    }

    async handleSubmit(e) {
        e.preventDefault()
        this.submitButton.current.setState({isSubmitting : true})
        this.setState({
            isSubmitting: true
        })
        if(await this.props.handleSubmit(this.state.newInfo) === true){
            this.setState({
                isSubmitted: true
            })

            setTimeout(()=>{
                this.setState({
                    isSubmitted: false,
                    isSubmitting: false
                })
            }, 2000)

        }else {
            this.drawResult()
            this.submitButton.current.setState({isSubmitting : false})
        }
    }

    recursiveCloneChildren(children) {
        return React.Children.map(children, child => {
            if(!React.isValidElement(child)) return child;
            let  childProps = {onChange: this.changeHolder.bind(this)};
            childProps.children = this.recursiveCloneChildren(child.props.children);
            return React.cloneElement(child, childProps);
        })
    }

    async componentDidMount() {

    }

    render() {
        if(!this.state.isSubmitted){
            return (

                    <form  onSubmit={this.handleSubmit.bind(this)} id={"myForm"}>
                        <Grid container  spacing={2} xs={12} >
                            <Grid container item  xs={10} spacing={2}>
                                {this.recursiveCloneChildren(this.props.children)}
                            </Grid>
                            <Grid item xs={2}>
                                <DynamicSubmitButton ref={this.submitButton}></DynamicSubmitButton>
                            </Grid>
                        </Grid>
                    </form>

            )
        }else{
            return (
                <div className={"YitTcpForm"} id={"YitTcpForm"}>
                    <DoneMsg/>
                </div>
            )
        }

    }
}
export default YitTcpForm