import React from 'react';
import "./YitFragment.css"
import CancelIcon from '@mui/icons-material/Cancel';
import ReactDOM from "react-dom";
import YitComponent from "../../Structures/YitComponent";
import CircularProgress from "@mui/material/CircularProgress";
import Button from "@mui/material/Button";
import ReplayIcon from '@mui/icons-material/Replay';
import Alert from "@mui/material/Alert";
import IconButton from "@mui/material/IconButton";
import {Close} from "@mui/icons-material";
class YitFragment extends YitComponent {
    constructor(props){
        super(props);
        this.state = {
            isLoading: this.props["isLoading"] === undefined ? true:this.props["isLoading"],
            failedLoading: false,
            notifications: new Map(),
        }
        this.notId = 0;
    }
    pushNotification(Msg, severity, duration){
        let id=this.notId
        this.state.notifications.set(
            id,
            <Alert
                id={id}
                severity={severity}
                sx={{mb: 2}}
                action={
                    <IconButton
                        aria-label="close"
                        color="inherit"
                        size="small"
                        onClick={() => {
                            this.state.notifications.delete(id)
                            this.setState({notifications: this.state.notifications})
                        }}
                    >
                        <Close fontSize="inherit" />
                    </IconButton>
                }
            >
                {Msg}
            </Alert>
        )
        this.notId++
        this.setState({notifications: this.state.notifications})
        if(duration !== undefined){
            setTimeout(()=>{
                this.state.notifications.delete(id)
                this.setState({notifications: this.state.notifications})
            }, duration)
        }
    }
    async componentDidMount() {


    }
    drawNotifications(map){
        let list = []
        map.forEach((value, key)=>{
            list.push(value)
        })
        return (list)
    }
    mountFragmentBackDrop(component){
        document.getElementById("BackDrop").addEventListener('click', ()=>{
            document.getElementById("BackDrop").style.display = "none"
        })
        document.getElementById('BackDropContent').addEventListener('click', (event)=>{
            event.stopPropagation();
        })
        document.getElementById("BackDrop").style.display = "flex"
        ReactDOM.render(component, document.getElementById("BackDropContent"))
    }

    unmount(component){
        document.getElementById("BackDrop").style.display = "none"
       // ReactDOM.render(component, document.getElementById("BackDropContent"))
    }

    drawOperations(){
        if(this.props.topOperations != undefined){
            return (this.props.topOperations.map(operation => {
                return (
                    <div style={{marginLeft: 10}}>{operation}</div>
                )
            }))
        }
    }


    render() {
        if(this.state.failedLoading){
            return (
                <div className={"failedToLoadPage"}>
                    <span>Failed to load Fragment primary data</span>
                    <br/>
                    <Button
                        style={{width: "10vw"}}
                        variant={"contained"}  startIcon={<ReplayIcon/>}
                        onClick={()=>this.props.primaryInfoLoader}
                    >
                        retry
                    </Button>
                </div>)
        }
        else {
            if(this.state.isLoading){
                return (<div className={"loadingPage"}><CircularProgress /></div>)
            }
            else {
                return (
                    <div className={'yitFragment'}>
                        <div className={"BackDrop"} id={"BackDrop"}>
                            <div className="close" onClick={this.unmount}><CancelIcon fontSize="large" style={{color: "red"}}/></div>
                            <div className="content" id={"BackDropContent"}></div>
                        </div>
                        <div className="pagesNotifications" id={"pagesNotifications"}>
                            {this.drawNotifications(this.state.notifications)}
                        </div>
                        <section className={`YitFragment ${this.props.customClass}`} id={this.props.customId}>
                                {this.props.children}
                        </section>
                   </div>
                )
            }
        }

    }
}

export default YitFragment