import React from 'react';
import ReactDOM from 'react-dom';
import "./RechargeCodes.css"
import MaterialTable, {MTableToolbar} from '@material-table/core';
import tableIcons from "../../Components/tableIcons";
//Icons

import {Link} from "react-router-dom";



import {ClientStats} from "../../Structures/ClientStats";
import {Notifications} from "../../Apis/Notifications";
import YitPage from "../../Components/YitPage/YitPage";
import YitComponent from "../../Structures/YitComponent";
import RechargeCodeStates from "../../Structures/RechargeCodeStates";

import TextField from "@material-ui/core/TextField";
import Select from "@mui/material/Select";
import MenuItem from "@material-ui/core/MenuItem";
import InputLabel from "@mui/material/InputLabel";
import FormControl from "@mui/material/FormControl";
import YitForm from "../../Components/YitForm/YitForm";
import StatBox from "../../Components/StatBox/StatBox";
import YitDataTable from "../../Components/YitDataTable/YitDataTable";
import YitFragment from "../../Components/YitFragment/YitFragment";
import MuiIcons from "@mui/icons-material";
import StationEditForm from "../Stations/StationEditForm/StationEditForm";
import TransactionOperations from "../../Structures/TransactionOperations";

const Icons = require("@mui/icons-material")




class RechargeCodes extends YitComponent {

    constructor(props){
        let t =  RechargeCodeStates.reduce((reducer ,reCodeState)=> {

            return  reducer + ` ${reCodeState.value} : "${reCodeState.label}", `
        }, '')

        super(props);

        this.state = {
            rawData: [],
            columns: [
                {title: 'Id', field: "id"},
                {
                    title: 'Stat',   field: "stat",
                    lookup: {0 : "New",  1 : "Printed",  2 : "Consumed"}
                },
                {title: 'Amount', field: "amount"},
                {title: 'Date', render: rowData =>rowData['createdAt'].substr(0, 10)},
            ],
            data: [],
            selectedRowId: null,
            open: false
        }

        this.submitButton = React.createRef()
    }

    async  doGenerate(data){
        let c = await this.MODELS._RechargeCode.create(data)
        if(c.finalResult === true){
            return true
        }else {
            return false
        }
    }

    async componentDidMount() {
        this.currentPage.current.setState({
            isLoading: false
        })
        let t = await this.dataModel.getAll(0, 50)

    }

    async changeStat(rowData, stat){
        try{
            let updateResult = await this.MODELS._Partner.update(rowData.id, {stat: stat})
            if(updateResult){
                Notifications.drawActionResult("pagesNotifications", "Stat Changed successfully", "success")
                let currentRow = this.state.data.indexOf(rowData)
                let newData = this.state.data
                newData[currentRow].stat = stat
                this.setState({
                    data: newData
                })

            }
        }catch (e){

        }
    }

    dataModel = {
        getAll : async (offset, limit) => {
            let result = await this.MODELS._RechargeCode.getAllForPartner(offset, limit, this.props.id)
            return result
        }
    }


    render() {
        return (
                <YitFragment
                    ref={this.currentPage}
                    customClass={"ReCodesPage"} customId={"ReCodesPage"} title={"Recharge Codes"}
                    customContentClass={"ReCodesContent"}>
                    <div className={"codesContainer"}>
                    <YitDataTable
                        columns={this.state.columns}
                        dataModel={this.dataModel}
                        options={{search: false, toolbar: false,  filtering: true, paginationPosition: "bottom", selection: true}}
                        actions={[
                            {
                                icon: ()=>"delete",
                                tooltip: 'Delete',
                                isFreeAction: true
                            }
                        ]}
                    >

                    </YitDataTable>
                    </div>
                </YitFragment>
        )
    }
}

export default RechargeCodes