import {SERVER} from "../Apis/GlobalConfig";
import server from "../config/globalConfig";
import RequestHandler from "../Apis/RequestHandler";


let actor = "Session/"
const _Admin  = {

    login: async (postData) => {
        try {
            const requestOptions = {
                method: 'POST',
                headers: {'Content-Type': 'application/json'},
                body: JSON.stringify(postData)
            };
            const response = await fetch(SERVER+"Guest/adminLogin", requestOptions);
            const data = await response.json();
            return data;
        } catch (e){
            return {finalResult: false, error: "Could not connect to the server"}
        }
    },

    HeartBit: async () => {
        try {
            let result = await RequestHandler.GET(SERVER+"HeartBit")
            return result
        } catch (e){
            return {finalResult : false, error: e}
        }
    }
}
export default _Admin;