import React from 'react';
import ReactDOM from 'react-dom';
import "./Partners.css"
import MaterialTable from '@material-table/core';
import tableIcons from "../../Components/tableIcons";
//Icons

import {Link} from "react-router-dom";

import {ClientStats} from "../../Structures/ClientStats";
import {Notifications} from "../../Apis/Notifications";
import YitPage from "../../Components/YitPage/YitPage";
import YitComponent from "../../Structures/YitComponent";
import NewStationForm from "../Stations/NewStationForm/NewStationForm";
import YitFragment from "../../Components/YitFragment/YitFragment";
import NewPartnerForm from "./NewPartnerForm/NewPartnerForm";
import YitDataTable from "../../Components/YitDataTable/YitDataTable";
import YitStepper from "../../Components/YitStepper/YitStepper";
import YitStep from "../../Components/YitStepper/YitStep";
import NewPartner from "./NewPartner/NewPartner";
import Chip from "@mui/material/Chip";

const MuiIcons = require("@mui/icons-material")

function statToString(stat) {
    let result = ""
    ClientStats.forEach((clientStat) => {
        if (clientStat.value == stat) {
            result = clientStat.label
        }
    })
    return result
}


class Partners extends YitComponent {

    constructor(props) {
        super(props);
        this.state = {
            rawData: [],
            columns: [
                {title: 'Name', field: "fullName"},
                {title: 'E-mail', field: "mail"},
                {title: 'Phone', field: "phone"},
                {title: 'Stat', field: "stat",
                    lookup: { 0: 'New', 1: 'Active', 2: "Trusted", 11: "Frozen" },
                    render: (rowData)=>{
                        let result, color
                        switch (rowData.stat){
                            case 0: result = 'New'; color='grey'; break;
                            case 1: result = 'Active'; color= 'darkblue'; break;
                            case 2: result = 'Trusted'; color="green"; break;
                            case 11: result = 'Frozen'; color="red"; break;
                            default:result = "Unknown"; color="red"
                        }
                        return(<Chip  label={result}  style={{width: 120, backgroundColor: color, color:"white", fontWeight: "bold"}}/>)
                    }
                },

            ],
            data: [],
            selectedRowId: null,
            open: false
        }
        this.dataTable = React.createRef()
    }

    handleClose = (event, reason) => {

    };

    async loadPartners() {
        let getOp = await this.MODELS._Partner.getAll(0, 20)
        if (getOp.finalResult) {
            let partnersList = getOp.result
            this.setState({
                failedLoading: false,
                data: partnersList
            })
        } else {

            this.setState({
                failedLoading: true
            })
        }

    }

    async loadPrimaryData() {
        await this.loadPartners()
        if (this.state.failedLoading) {

            this.currentPage.current.setState({
                failedLoading: true
            })
        } else {
            this.currentPage.current.setState({
                failedLoading: false,
                isLoading: false
            })
        }

    }

    async componentWillMount() {
        await this.loadPrimaryData()

    }

    async componentDidMount() {


    }
    async updatePartnerInfo(newData){
        try{
            let updateOp = await this.MODELS._Partner.update(newData.id, newData)
            if(updateOp.finalResult){
                Notifications.drawActionResult("pagesNotifications", "Partner Updated", "success")
                this.dataTable.current.tableRef.current.onQueryChange()
            }else{
                Notifications.drawActionResult("pagesNotifications", "Could update Client", "error")
            }
        }catch (e){
            Notifications.drawActionResult("pagesNotifications", "Could not change client info", "error")
        }
    }

    async changeStat(rowData, stat) {
        try {
            let updateResult = await this.MODELS._Partner.update(rowData.id, {stat: stat})
            if (updateResult) {
                Notifications.drawActionResult("pagesNotifications", "Stat Changed successfully", "success")
                this.dataTable.current.tableRef.current.onQueryChange()
            }
        } catch (error) {

        }
    }

    async deleteEntry(rowData, stat) {
        try {
            let updateResult = await this.MODELS._Partner.delete(rowData.id)
            if (updateResult) {
                Notifications.drawActionResult("pagesNotifications", "Partner Deleted", "success")
                this.dataTable.current.tableRef.current.onQueryChange({})
            }
        } catch (error) {

        }
    }

    render() {
        return (

            <YitPage
                ref={this.currentPage}
                customClass={"ClientsPage"} customId={"ClientsPage"} title={"Partners"}
                reloadPrimaryInfo={async () => {
                    await this.loadPrimaryData()
                }}
                topOperations={[
                    {
                        text: "New Partner",
                        icon: <MuiIcons.Add />,
                        onClick: ()=>{this.currentPage.current.mountBackDrop(<NewPartnerForm/>)}
                    }

                ]}
            >

                <YitDataTable
                    ref={this.dataTable}
                    columns={this.state.columns}
                    actions={[
                        (rowData) => {
                            switch (rowData.stat) {
                                case 0:
                                    return {
                                        icon: () => <MuiIcons.Check style={{color: "blue"}}></MuiIcons.Check>,
                                        onClick: (event, rowData) => this.changeStat(rowData, 1),
                                        tooltip: "validate",
                                    }
                                    break
                                case 1:
                                    return {
                                        icon: () => <MuiIcons.Block style={{color: "red"}}></MuiIcons.Block>,
                                        tooltip: "Freeze User",
                                        onClick: (event, rowData) => this.changeStat(rowData, 2)
                                    }
                                case 2:
                                    return {
                                        icon: () => <MuiIcons.Beenhere style={{color: "green"}}></MuiIcons.Beenhere>,
                                        tooltip: "Reactivate User",
                                        onClick: (event, rowData) => this.changeStat(rowData, 1)
                                    }
                            }

                        },
                        (rowData) =>{return{
                            icon: () => <MuiIcons.Delete style={{color: "red"}}></MuiIcons.Delete>,
                            onClick: (event, rowData) => {

                                this.deleteEntry(rowData).then(r => {})
                                //this.props.history.push("/Dashboard/Clients/", { state: 'sample data'});
                            },
                            tooltip: "validate",
                        }},

                        (rowData) => {
                            return {
                                icon: () => <Link to={{
                                    pathname: "/Dashboard/PartnerDetails/" + rowData.id,
                                    search: '',
                                    state: {data: this.state.rawData[rowData.rowIndex]}
                                }}><MuiIcons.Launch style={{color: "black"}}/></Link>,
                                tooltip: "Details",
                            }
                        }
                    ]}
                    onRowClick={(event, rowData) => {
                        (event.target).ondblclick = () => {
                            this.props.history.push("/Dashboard/PartnerDetails/" + rowData.id, {  state: {data: this.state.rawData[rowData.rowIndex]}});

                        }

                    }}
                    dataModel={this.MODELS._Partner}
                    searchEntry={'fullName'}
                    editable={{
                        onRowUpdate: (newData, oldData) => this.updatePartnerInfo(newData),
                        onRowDelete: oldData =>
                            new Promise((resolve, reject) => {
                                this.deleteEntry(oldData).then(r => resolve())
                            })
                    }}

                />
            </YitPage>


        )
    }
}

export default Partners