import React from "react";
import "./StationEditForm.css"
import TextField from "@material-ui/core/TextField";
import Grid from "@mui/material/Grid";
import Button from "@mui/material/Button";
import YitComponent from "../../../Structures/YitComponent";
import {Notifications} from "../../../Apis/Notifications";
import TabsWidget from "../../../Components/TabsWidget/TabsWidget";
import YitFragment from "../../../Components/YitFragment/YitFragment";
import YitForm from "../../../Components/YitForm/YitForm";
import Select from "@mui/material/Select";
import MenuItem from "@material-ui/core/MenuItem";
import InputLabel from "@mui/material/InputLabel";
import FormControl from "@mui/material/FormControl";
import YitTcpForm from "../../../Components/YitTcpForm/YitTcpForm";
import {VolumeDown, VolumeUp} from "@mui/icons-material";
import Slider from "@mui/material/Slider";

const MuiIcons = require('@mui/icons-material');

const APNView =  ({APN,  id})=>{
    if(APN == undefined){
        APN = {}
    }

    return(
        <div className={"PowerBankView"} id={id} >
            <YitForm>
                <Grid container spacing={2} xs={12} style={{width: "100%"}}>
                    <Grid item xs={6}>
                        <TextField
                            fullWidth
                            id="outlined-error-helper-text"
                            label="MCCMNC"
                            defaultValue={APN.MCCMNC}
                            size="small"
                            InputProps={{
                                readOnly: true,
                            }}>
                        </TextField>
                    </Grid>
                    <Grid item xs={6}>
                        <TextField
                            fullWidth
                            id="outlined-error-helper-text"
                            label="Name"
                            defaultValue={APN.APN}
                            size="small"
                            InputProps={{
                                readOnly: true,
                            }}>
                        </TextField>
                    </Grid>
                    <Grid item xs={12}>

                    </Grid>
                    <Grid item xs={6}>
                        <TextField
                            fullWidth
                            id="outlined-error-helper-text"
                            label="User Name"
                            defaultValue={APN.APN}
                            size="small"
                            InputProps={{
                                readOnly: true,
                            }}>
                        </TextField>
                    </Grid>
                    <Grid item xs={6}>
                        <TextField
                            fullWidth
                            id="outlined-error-helper-text"
                            label="Password"
                            defaultValue={APN.APN}
                            size="small"
                            InputProps={{
                                readOnly: true,
                            }}>
                        </TextField>
                    </Grid>
                </Grid>
            </YitForm>

        </div>
    )
}

class StationEditForm extends YitComponent{
    constructor(props) {
        super(props);
        this.state = {
            isLoading: true,
            failedLoading : false,
            currentStation: {Partner: {}},
            currentStationInfo: {},
            currentTab :"one",
            stationAPNs : [],

            partnersList: [],
            statsList: [
                {label: "active", id: 0},
                {label: "Frozen", id: 1}
                ]
            }

        this.fragment = React.createRef()
        if(this.state.currentStation === undefined){
            this.state.currentStation = {}
        }else {
            this.state.currentStation = this.props.station
            this.loadPrimaryData().then(r => {})
        }
    }

    async loadPartners(){
        let getAllOp = await this.MODELS._Partner.getAll(0, 999)
        if(getAllOp.finalResult !== false){
            let partners = getAllOp.result.rows
            let prepared = partners.map(partner =>{
                return{value: partner.id, label: partner.fullName}
            })
            this.setState({
                partnersList: prepared
            })
        }else {
            this.setState({
                failedLoading : true
            })
            return false
        }
    }

    async loadPrimaryData(){

        await this.loadPartners();
        if(this.state.failedLoading){
            this.fragment.current.setState({
                failedLoading: true
            })
        }else {
            this.fragment.current.setState({
                isLoading: false
            })
        }
    }

    async componentDidMount() {

    }

    static getDerivedStateFromProps(){

    }

    async loadStationInfo(id) {
        let getOneOP = await this.MODELS.station.getOnById(id)
        if (getOneOP.finalResult === true) {
            let currentStation = getOneOP.result

            this.setState({
                currentStation: currentStation,
            })
            return true
        } else {
            this.setState({
                failedLoading : true
            })
            return false
        }
    }

    async loadPowerBanksInfo(id) {
        let currentStationInfo = await this.MODELS._Station.getOneRealTime(id)
        if (currentStationInfo != false) {
            this.setState({
                currentStationInfo: currentStationInfo,
            })
        } else {
            Notifications.drawActionResult("pagesNotifications", "Could not Load Station primary  Data", "error")
        }
    }

    async loadAPN(stationId, index) {
        let APN = await this.MODELS._Station.queryAPNNs(stationId, index)
        if(APN != false){
            let current  = this.state.stationAPNs
            current[index] = APN
            this.setState({
                stationAPNs: current
            })
        }else {
            Notifications.drawActionResult("pagesNotifications", "Could not Load APN info", "error")
        }
    }

    async handleSubmitStationData(newData) {
        let updateOp = await this.MODELS._Station.update(this.state.currentStation.systemId, newData)
        if(updateOp.finalResult === true){
            return true
        }else {
            return  false
        }
    }

    async handleSetAddress(newData) {
        let updateOp = await this.MODELS._Station.setAddress(this.state.currentStation.id, newData)
        if(updateOp.finalResult === true){
            return true
        }else {
            return  false
        }
    }

    async handleSetVolume(newData) {
        let updateOp = await this.MODELS._Station.setVolume(this.state.currentStation.id, newData.level)
        if(updateOp.finalResult === true){
            return true
        }else {
            return  false
        }
    }

    drawAPN(){
        this.MODELS.station.queryAPNNs(this.state.currentStation.id, "00").then((result)=>{
            if(result != false){
                return <APNView APN={result}></APNView>
            }else {
                Notifications.drawActionResult(
                    "pagesNotifications",
                    "Could not load APN info",
                    "error",
                    "standard",
                    null)
            }
        })
    }

    render() {
        return (
            <YitFragment
                ref={this.fragment}
                customClass={"StationEditForm"}
                customId={"StationEditForm"}
                title={"Station Details"}
                primaryInfoLoader={()=>{this.loadPrimaryData().then(r => {})}}
            >


                <div className="hardCodedInfoHolder">
                    <TabsWidget
                        tabsList={[
                            {title: "Primary Info"},
                            {title: "Server configuration"},
                            {title: "APNS"},
                            {title: "Sound Level"},
                        ]}
                    >
                        <YitForm
                            handleSubmit={this.handleSubmitStationData.bind(this)}
                        >
                            <Grid container spacing={2} xs={12}>
                                <Grid item xs={12}><b>Station Primary data</b></Grid>
                                <Grid item xs={5}>
                                    <TextField
                                        disabled fullWidth required variant={"outlined"}
                                        id="outlined-required" label="SystemId" defaultValue={this.state.currentStation.systemId}
                                    >
                                    </TextField>
                                </Grid>
                                <Grid item xs={7}>
                                    <TextField
                                        fullWidth required variant={"outlined"}
                                        name={"id"} id="outlined-required" label="Public Id" defaultValue={this.state.currentStation.id}
                                    />
                                </Grid>
                                <Grid item xs={5}>
                                    <FormControl fullWidth>
                                        <InputLabel id="demo-simple-select-label">Partner</InputLabel>
                                        <Select
                                            labelId="demo-simple-select-label"
                                            id="currentPartner"
                                            label="Partner"
                                            name={"currentPartner"}
                                            defaultValue={this.state.currentStation.currentPartner}

                                        >
                                            {this.state.partnersList.map((option) => (
                                                <MenuItem value={option.value}> {option.label}</MenuItem>
                                            ))}
                                            <MenuItem value={10}>Ten</MenuItem>
                                        </Select>
                                    </FormControl>
                                </Grid>
                                <Grid item xs={3}>
                                    <FormControl fullWidth>
                                        <InputLabel id="demo-simple-select-label">Stat</InputLabel>
                                        <Select
                                            labelId="demo-simple-select-label"
                                            id="demo-simple-select"
                                            name={"stat"}
                                            label="Stat"
                                            defaultValue={this.state.currentStation.stat}
                                        >
                                            {this.state.statsList.map((option) => (
                                                <MenuItem value={option.id}> {option.label}</MenuItem>
                                            ))}
                                        </Select>
                                    </FormControl>
                                </Grid>
                                <Grid item xs={4}>
                                    <TextField
                                        fullWidth required variant={"outlined"} type={"number"}
                                        id="outlined-required" name="price" label="Price" defaultValue={this.state.currentStation.price}
                                    >
                                    </TextField>
                                </Grid>
                            </Grid>
                        </YitForm>
                        <YitTcpForm
                            handleSubmit={this.handleSetAddress.bind(this)}>
                            <Grid item xs={6}>
                                <TextField
                                    fullWidth size={"small"} name={"address"}
                                    required id="outlined-required" label="Server address" placeholder="xxx.xxx.xxx.xxx"
                                >
                                </TextField>
                            </Grid>
                            <Grid item xs={3}>
                                <TextField
                                    fullWidth required type={"number"} name={"port"} size={"small"}
                                    id="outlined-required" label="Port Number" defaultValue="4000"
                                />
                            </Grid>
                            <Grid item xs={3}>
                                <TextField
                                    fullWidth required type={"number"}  name={"heartBit"} size={"small"}
                                    id="outlined-required" label="Heart Bit" defaultValue="30"
                                />
                            </Grid>
                        </YitTcpForm>
                        <TabsWidget
                            tabsList={[
                                {
                                    title: "First",

                                },
                                {
                                    title: "Second",
                                },
                                {
                                    title: "Third",
                                },
                                {
                                    title: "Forth",
                                },
                                {
                                    title: "Fifth",
                                },

                            ]}
                        >
                            <div id="firstHolder"><APNView APN={this.state.stationAPNs[0]}/></div>
                            <div id="secondHolder"><APNView APN={this.state.stationAPNs[1]}/></div>
                            <div id="thirdHolder"><APNView APN={this.state.stationAPNs[2]}/></div>
                            <div id="forthHolder"><APNView APN={this.state.stationAPNs[3]}/></div>
                            <div id="fifthHolder"><APNView APN={this.state.stationAPNs[4]}/></div>
                        </TabsWidget>
                        <YitTcpForm
                            view={this.fragment.current}
                            errorMsg={"Could not set volume correctly"}
                            handleSubmit={this.handleSetVolume.bind(this)}
                        >
                            <Grid item xs={12}>
                                <div style={{display: "flex"}}>
                                    <VolumeDown />
                                    <Slider
                                        name={"level"}
                                        min={0}
                                        max={15}
                                        aria-label="Volume"
                                        onChange={""} />
                                    <VolumeUp />
                                </div>
                            </Grid>
                        </YitTcpForm>
                    </TabsWidget>
                </div>
            </YitFragment>
        )
    }
}
export default StationEditForm

