import {SERVER, ACTOR} from "../Apis/GlobalConfig";
import RequestHandler from "../Apis/RequestHandler";
import React from "react";


class _Model {

    constructor(adminToken, MODEL) {

        this.adminToken =  adminToken
        this.MAIN_ENTRY = SERVER+ACTOR+MODEL
    }

    create = async(postData) =>{
        const URL = this.MAIN_ENTRY+"create"
        let result = await RequestHandler.POST(URL, postData, this.adminToken)
        return result

    }

    update = async(id, newData) =>{
        const URL = this.MAIN_ENTRY+"update/"+id
        let result = await RequestHandler.POST(URL, newData, this.adminToken)
        return result
    }

    async getAll(offset, index) {

        try{
            const URL = this.MAIN_ENTRY+"getAll/"+offset+"/"+index
            let result = await RequestHandler.GET(URL, this.adminToken)
            return result
        }catch (error){
            console.log(error)
            return {finalResult: false}
        }
    }

    async getOnById(id) {
        try {
            const URL = this.MAIN_ENTRY+"getOne/"+id
            let result = await RequestHandler.GET(URL, this.adminToken)
            return result
        }
        catch (error){
            return {finalResult: false}
        }
    }

    async searchBy(attribute, key) {
        try{
            const URL = this.MAIN_ENTRY+"searchBy/"+attribute+"/"+key
            let result = await RequestHandler.GET(URL, this.adminToken)
            return result
        }catch (error){
            return {finalResult: false}
        }
    }

    async count() {
        try{
            const URL = this.MAIN_ENTRY+"count"
            let result = await RequestHandler.GET(URL, this.adminToken)
            return result

        }catch (error){
            return {finalResult: false}
        }
    }

    async countWhere(attribute, value) {
        try{
            const URL = this.MAIN_ENTRY+"count/"+attribute+"/"+value
            let result = await RequestHandler.GET(URL, this.adminToken)
            return result

        }catch (error){
            return {finalResult: false}
        }
    }

    async delete(id) {
        try{
            const URL = this.MAIN_ENTRY+"delete/"+id
            let result = await RequestHandler.GET(URL, this.adminToken)
            return result

        }catch (error){
            return {finalResult: false}
        }
    }
}

export default _Model;