import {SERVER, ACTOR} from "../Apis/GlobalConfig";
import RequestHandler from "../Apis/RequestHandler";
import _Model from "./_Model";
const MODEL = "RechargeCode/"
const MAIN_ENTRY = SERVER+ACTOR+MODEL
class RechargeCode extends  _Model{

    constructor(adminToken) {
        super(adminToken, MODEL)
        this.adminToken =  adminToken
    }

    async getOnById(id) {
        const URL = MAIN_ENTRY+"getOne/"+id
        const requestOptions = {
            method: 'GET',
            headers: {'Content-Type': 'application/json'},
        };
        const response = await fetch(URL, requestOptions);
        const data = await response.json();
        if (data.finalResult == true) {
            return data.result;
        } else {
            return null;
        }
    }

    async getAllForPartner(offset, index, partnerId) {
        try{
            const URL = this.MAIN_ENTRY+"getAll/"+offset+"/"+index+"/"+partnerId
            let result = await RequestHandler.GET(URL, this.adminToken)
            return result
        }catch (error){
            console.log(error)
            return {finalResult: false}
        }
    }

}

export default RechargeCode;