import React from 'react';
import "./Stations.css"
import ReactDOM from "react-dom";
import YitPage from "../../Components/YitPage/YitPage";
import YitComponent from "../../Structures/YitComponent";
import NewStationForm from "./NewStationForm/NewStationForm";
import Chip from "@mui/material/Chip";
import StationDetails from "./StationDetails/StationDetails";
import StationEditForm from "./StationEditForm/StationEditForm";
import YitDataTable from "../../Components/YitDataTable/YitDataTable";
const MuiIcons = require("@mui/icons-material")

class Stations extends YitComponent {
    constructor(props){
        super(props);
        this.state = {
            failedLoading : false,
            columns: [
                {title: 'SystemId', field: 'systemId'},
                {title: 'ID', field: 'id'},
                {
                    title: 'Stat', field: 'stat',
                    lookup: {
                        0: <Chip  label="Active" color={"primary"} style={{width: 120}}/>,
                        1: <Chip variant={"outlined"} label="Offline"  style={{width: 120, color: "red"}} />
                    }
                },
                {
                    title: 'Rent Fees', field: 'price',
                    render: rowData => rowData['price'] + " Units"
                },
                {title: 'Added On', render: rowData => rowData['createdAt'].substr(0, 10)},
            ],
            stationsList : [],
            page: 0,
            totalCount: 0
        }
        const urlParams = new URLSearchParams(window.location.search);
        this.tableRef = React.createRef();
    }

    async loadPrimaryData() {
        if(this.state.failedLoading){
            this.currentPage.current.setState({
                failedLoading: true
            })
        }else {
            this.currentPage.current.setState({
                failedLoading: false,
                isLoading: false
            })
        }
    }

    async componentDidMount() {
        await this.loadPrimaryData()
        setTimeout(()=>{
            // this.tableRef.current.onToggleDetailPanel([0], rowData => <StationDetails id={rowData.systemId}/>)
            console.log(this.tableRef.current)
        }, 3000)

    }


    componentWillUnmount() {

    }

    setValid = async() =>{
        let result = [];
        let data = this.state.rawData;
        for(let i=0; i<data.length; i++){
            if (parseInt(data[i].valid) == 1){
                result.push(data[i])
            }
        }
        this.setState({data: result})
    }

    render() {
        return (
        <>
            <YitPage
                ref={this.currentPage}
                title={"Stations"}
                customClass={"StationsPage"}
                customdId={"StationsPage"}
                contentCustomId={"StationsPageContent"}
                reloadPrimaryInfo={()=>{
                    this.loadPrimaryData()
                }}
                topOperations={[
                    {
                        text: "New Station",
                        icon : <MuiIcons.Add/>,
                        onClick: ()=>{this.currentPage.current.mountBackDrop(<NewStationForm></NewStationForm>)}
                    }
                ]}
                reloadPrimaryInfo={()=>this.loadPrimaryData()}
            >
                <YitDataTable
                    ref={this.tableRef}
                    columns={this.state.columns}
                    actions={[
                        {
                            icon: ()=><MuiIcons.Block style={{color: "darkred"}}/>,
                            tooltip: 'Delete',
                        },
                        {
                            icon: ()=><MuiIcons.Delete style={{color: "darkred"}}/>,
                            tooltip: 'Delete',
                        },
                        {
                            icon: () => <MuiIcons.Edit style={{color: ""}}/>,
                            tooltip: "Edit",
                            onClick: (event, data) => this.currentPage.current.mountBackDrop(<StationEditForm station={data}/>)
                        }
                    ]}
                    dataModel={this.MODELS._Station}
                    detailPanel={(rowData) => {
                        return (
                            <div className={"stationDetailsHolder"}>
                                <StationDetails id={rowData['rowData'].id}/>
                            </div>
                        )
                    }}

                />
            </YitPage>
            </>


        )
    }
}

export default Stations